.navbar {
  min-height: 52px;
  padding: 10px 20px;
  /* position: absolute;
  top: 0;
  left: 0;
  right: 0; */
  font-size: 24px;
  background-color: white;
  box-shadow: 0 1px 0 rgba(0,0,2,.23);
}

.navbar-container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  margin-top: 10px;
  background-color: #fff;
}

.navbar-header {
  float: left;
  height: 32px;
  background-color: #fff;
  position: relative;
}

.navbar-toggle {
  position: absolute;
  top: 0;
  right: 35px;
  bottom: 0;
  margin-bottom: 10px;
  background-color: transparent;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  display: none;
}

.navbar-toggle .icon-bar {
  display: block;
  width: 22px;
  height: 2px;
  border-radius: 1px;
}

.icon-bar {
  background-color: #222222;
}

.navbar-toggle .icon-bar+.icon-bar {
  margin-top: 4px;
}

.navbar-logo {
  color: black;
  text-decoration: none;
  height: 52px;
}

.navbar-logo .navbar-img {
  height: 120%;
  width: auto;
  display: absolute;
}

.navbar-collapse .navbar-center {
  margin: 0 auto 0 40px;
  padding: 0;
  list-style: none;
  display: inline;
}

.navbar-collapse .navbar-center li,
.navbar-collapse .navbar-right li {
  display: inline;
  margin: 10px;
}

.navbar-collapse .navbar-center li a,
.navbar-collapse .navbar-right li a {
  text-decoration: none;
  color: black;
}

.navbar-collapse .navbar-right {
  margin: 0;
  padding: 0;
  list-style: none;
  float: right;
}

.navbar-collapse .navbar-center li a:hover, 
.navbar-collapse .navbar-right li a:hover {
  opacity: 0.5;
}

@media only screen and (max-width: 720px) {
  .navbar {
    padding: 0;
  }

  .navbar-container {
    padding: 0;
    margin: 0;
  }

  .navbar-header {
    float: none;
    z-index: 2;
    height: 52px;
    padding: 15px 20px;
  }

  .navbar-logo {
    float: none;
    display: block;
    text-align: left;
    height: 42px;
  }

  .navbar-toggle {
    display: block;
  }

  .navbar-collapse {
    transition: all 0.8s ease;
    background-color: #fff;
    width: 100%;
    height: auto;
    padding-bottom: 10px;
    position: absolute;
    box-shadow: 0 1px 0 rgba(0,0,2,.23);
    top: -100%;
    left: 0;
    z-index: 1;
  }

  .navbar-collapse .navbar-center, 
  .navbar-collapse .navbar-right {
    margin: 0;
    padding: 0;
    text-align: left;
    list-style: none;
    display: block;
    float: none;
  }

  .navbar-collapse .navbar-center li,
  .navbar-collapse .navbar-right li {
    display: block;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    padding-left: 25px;
  }

  .navbar-collapse .navbar-center li a:active, 
  .navbar-collapse .navbar-right li a:active {
    opacity: 0.5;
  }

  .navbar-collapse.active{
    transition: all 0.8s ease;
    background-color: #fff;
    top: 72px;
  }
}
